import React, {Component} from 'react';
// import axios from 'axios';
import {
  Form,
  Input,
  Button,
  Row,
  Col,
  Icon,
  message
} from 'antd';
message.config({
  top: 200,
});
const publics = require('./publics'); //
const phoneNum = publics.phoneNum; //

class getCodes extends Component {
  constructor(props) {
    super();
    this.state = {
      getCodesState: true,        //true  
      codeNum: 60,                // 60 
      TestGetCode: "",
      vCoden: null,
      placeholder: "",
    }
  }

  // 
  vCode = (e) => {
    this.setState({
      vCoden: e.target.value
    })
  }

  getCodeBtn = () => {
    let phoneNumbern = this.props.phoneNumbern;         //
    let dataCode_ = this.state;
    let this_ = this;   //  this
    if (phoneNumbern === undefined) {
      message.error("");
    } else if (!phoneNum.test(phoneNumbern)) {
      message.error("");
    } else {
      //  
      let codeNum = dataCode_.codeNum;
      const timer = setInterval(() => {
      this_.setState({
        getCodesState:false,
        codeNum: (codeNum--)
        }, () => {
            if (codeNum === 0) {
            clearInterval(timer);
            this_.setState({
              getCodesState: true,
              codeNum: 60,
              TestGetCode: ""
            })
          }
        })
      }, 1000)
      }
  }

  render() {
    return (<div>
      <Row gutter={16}>
        <Col span={15}>
          <Input className="register-input" prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" onChange={this.props.vCode}/>
        </Col>
        <Col span={9}>
          {
            this.state.getCodesState
              ? <Button style={{ marginTop: '0', fontSize: '12px', padding: '0 9px' }} onClick={this.getCodeBtn}>{ this.state.TestGetCode }</Button>
              : <Button style={{ marginTop: '0' }} disabled="disabled">{this.state.codeNum}</Button>
          }
        </Col>
      </Row>
    </div>)
  }
}

const getCode = Form.create()(getCodes);
export default getCode

