import React, { Component } from 'react';
import { Form, Input, Tabs ,Button, Checkbox, Icon, message } from 'antd';
import { Link } from 'react-router-dom';

import CodeLogin from './codeLogin';          //
// import axios from 'axios';
import './login.css';

const publics = require('../../component/publics'); //
const phoneNum = publics.phoneNum; //

// tabs
const TabPane = Tabs.TabPane;

class Logins extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log(values);
        if ( !phoneNum.test(values.userName)) {
          message.error("")
        } else {
          // 
          this.props.history.push('../center_new/center_new');
          // key
          localStorage.removeItem("key")
        }
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="login">
        {/* header */}
        <div className="login-header">
          <div className="login-header-left">
            <img src={require("../../imgs/logo.png")} alt="logo"/>
            <span></span>
          </div>
          <div className="login-header-right">
            <span></span>
            <Link to="/register"></Link>
          </div>
        </div>
        {/* content */}
        <div className="login-content">
          {/*  */}
          <div className="content-left">
            {/* <img src={require("../../imgs/logo.png")} alt="logo"/> */}
          </div>
          {/*  */}
          <div className="content-right">
            <Tabs defaultActiveKey="1" onChange={this.callback}>
              {/*  */}
              <TabPane tab="" key="1">
                <Form onSubmit={this.handleSubmit} className="login-form loginPage">
                  <Form.Item>
                    {getFieldDecorator('userName', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="\" />
                    )}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator('password', {
                      rules: [{ required: true, message: '!' }],
                    })(
                      <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="" />
                    )}
                  </Form.Item>
                  <Form.Item>
                    {getFieldDecorator('remember', {
                      valuePropName: 'checked',
                      initialValue: true,
                    })(
                      <Checkbox></Checkbox>
                    )}
                    <Link className="login-form-forgot" to="/"></Link>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                      
                    </Button>
                     <Link to="/register">!</Link>
                  </Form.Item>
                </Form>
              </TabPane>
              {/*  */}
              <TabPane tab="" key="2">
                <CodeLogin />
              </TabPane>
            </Tabs>
          </div>
        </div>
      </div>
    )
  }
}

const Login = Form.create()(Logins);
export default Login
