import React, { Component } from 'react';
import { Form, Input ,Button, Icon } from 'antd';
import { Link } from 'react-router-dom';

import GetCode from '../../component/getCode';            //
class CodeLogins extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  // 
  vCode = (e) => {
    console.log(e);
    this.setState({
      vCoden: e.target.value
    })
  }
  // 
  phoneNumber = (e) => {
    this.setState({
      phoneNumbern: e.target.value
    })
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        console.log(this.state.vCoden);
      }
    });
  }

  render() {
    const { phoneNumbern } = this.state;
    const { getFieldDecorator } = this.props.form;
    return(
      <div>
        <Form onSubmit={this.handleSubmit} className="login-form loginPage">
          <Form.Item>
            {getFieldDecorator('userPhone', {
              rules: [{ required: true, message: '!' }],
            })(
              <Input onChange={this.phoneNumber} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
            )}
          </Form.Item>
          <Form.Item>
            {/*  */}
            {/* {
              vCoden === null ?
                getFieldDecorator('noteCode', {
              rules: [{ required: true, message: '!' }],
                })(
              <GetCode phoneNumbern={phoneNumbern}  />
                )
              :
              <GetCode phoneNumbern={phoneNumbern} vCode={this.vCode} />
            } */}
            <GetCode phoneNumbern={phoneNumbern}  />
            {/* <Row gutter={16}>
              <Col span={14}>
                {
              getFieldDecorator('captcha', {
              rules: [
              {
              required: true,
              message: '!'
              }
              ]
              })(<Input className="register-input" placeholder="" onChange={this.vCode} />)
                }
              </Col>
              <Col span={10}>
                {
              this.state.getCodesState
              ? <Button style={{ marginTop: '0' }} onClick={this.getCodes}>{ this.state.TestGetCode }</Button>
              : <Button style={{ marginTop: '0' }} disabled="disabled">{this.state.codeNum}</Button>
                }
              </Col>
            </Row> */}
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" className="login-form-button">
              
            </Button>
             <Link to="/register">!</Link>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

const CodeLogin = Form.create()(CodeLogins);
export default CodeLogin
