import React, { Component } from 'react';
import { Form, Input ,Button, Icon, Row, Col } from 'antd';
import { Link } from 'react-router-dom';
import GetCode from '../../component/getCode';            //

import "../login/login.css";

class Registers extends Component {
  constructor() {
    super();
    this.state = {
    }
  }

  // 
  phoneNumber = (e) => {
    this.setState({
      phoneNumbern: e.target.value
    })
  }

  // 
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    const { phoneNumbern } = this.state;
    const { getFieldDecorator } = this.props.form;
    return(
      <div className="login">
        {/* header */}
        <div className="login-header">
          <div className="login-header-left">
            <img src={require("../../imgs/logo.png")} alt="logo"/>
            <span></span>
          </div>
          <div className="login-header-right">
            <span></span>
            <Link to="/"></Link>
          </div>
        </div>
        {/* content */}
        <div className="register-content">
          {/*  */}
          <div className="register-left">
            {/* <h1></h1>
              <p>1.</p>
              <p>2.(1-20)</p>
              <p>3.(6)</p>
            <p>4.</p> */}
            <h1></h1>
            <p>1.</p>
            <p>2.</p>
            <p>3.</p>
            <p>4.</p>
            <img src={require("../../imgs/registerImg.jpg")} alt=""/>
          </div>
          {/*  */}
          <div className="register-right">
            {/* <h1></h1> */}
            <h1></h1>
            <Form onSubmit={this.handleSubmit} className="login-form">
              <Form.Item>
                {getFieldDecorator('Rcode', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input prefix={<Icon type="qrcode" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('cellNumber', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input onChange={this.phoneNumber} prefix={<Icon type="phone" style={{ color: 'rgba(0,0,0,.25)' }} />} maxLength={11} placeholder="" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('tuCode', {
                  rules: [{required: true,message: '!'}]
                })(
                  <Row gutter={16}>
                    <Col span={12}>
                      <Input className="register-input" prefix={<Icon type="picture" style={{ color: 'rgba(0,0,0,.25)' }} />} onChange={ this.tuCodes } placeholder=""/>
                    </Col>
                    <Col span={12}>
                      <img style={{ width: "100%" }} src={ require("../../imgs/captchaImg.png") } alt=""/>
                    </Col>
                  </Row>
                )}
              </Form.Item>
              <Form.Item>
                <GetCode phoneNumbern={phoneNumbern}  />
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('password', {
                  rules: [{ required: true, message: '!' }],
                })(
                  <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="6-16" />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('qq', {
                  rules: [{ required: true, message: 'QQ!' }],
                })(
                  <Input prefix={<Icon type="qq" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="QQ" />
                )}
              </Form.Item>
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-form-button">
                  
                </Button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    )
  }
}

const Register = Form.create()(Registers);
export default Register
