import React, {Component} from 'react';
import { Select, Form, Button, Tree, Pagination} from 'antd';
import { Link } from 'react-router-dom';

import PageHeader from '../../../../component/page_header/page_header'; //
import Menus from '../../../../component/menus/menus'; //

const Option = Select.Option;
const { TreeNode } = Tree;    //

class PublishTaskss extends Component {
  constructor() {
    super();
    this.state = {}
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (<div>
      {/*  */}
      <PageHeader/>
      <div className="rawp">
        <div className="contents">
          <div className="contents_left">
            <Menus history={this.props.history}/>
          </div>
          <div className="contents_right">
            {/*  */}
            <div className="shipment">
              <h2>PublishTasks</h2>
              <hr></hr>
              <Form onSubmit={this.handleSubmit} className="login-form">
                <Form.Item>
                  {
                    getFieldDecorator('platform', {
                      rules: [
                      {
                          required: true,
                          message: 'Please input your username!'
                      }
                      ]
                    })(<div>
                      
                      <Select defaultValue="" style={{
                          width: 180
                      }} onChange={this.handleChange}>
                        <Option value="jack"></Option>
                        <Option value="lucy">Lucy</Option>
                      </Select>
                    </div>)
                  }
                </Form.Item>
                <Form.Item>
                  {
                    getFieldDecorator('shops', {
                      rules: [
                      {
                          required: true,
                          message: 'Please input your Password!'
                      }
                      ]
                    })(<div>
                      
                      <Select defaultValue="shops" style={{
                          width: 260
                      }}>
                        <Option value="jack">Jack</Option>
                        <Option value="lucy">Lucy</Option>
                      </Select>
                    </div>)
                  }
                </Form.Item>
                <Form.Item>
                  {
                    getFieldDecorator('taskType', {
                      rules: [
                      {
                          required: true,
                          message: 'Please input your Password!'
                      }
                      ]
                    })(<div>
                      
                      <Select defaultValue="taskType" style={{
                          width: 180
                      }} onChange={this.handleChange}>
                        <Option value="jack">Jack</Option>
                        <Option value="lucy">Lucy</Option>
                      </Select>
                    </div>)
                  }
                </Form.Item>
                <Form.Item>
                  {
                    getFieldDecorator('terminal', {
                      rules: [
                      {
                          required: true,
                          message: 'Please input your Password!'
                      }
                      ]
                    })(<div>
                      
                      <Select defaultValue="terminal" style={{
                          width: 120
                      }}>
                        <Option value="jack">Jack</Option>
                        <Option value="lucy">Lucy</Option>
                      </Select>
                    </div>)
                  }
                </Form.Item>
                <Form.Item>
                  {
                    getFieldDecorator('taskState', {
                      rules: [
                      {
                          required: true,
                          message: 'Please input your Password!'
                      }
                      ]
                    })(<div>
                      
                      <Select defaultValue="taskState" style={{
                          width: 120
                      }}>
                        <Option value="jack">Jack</Option>
                        <Option value="lucy">Lucy</Option>
                      </Select>
                    </div>)
                  }
                </Form.Item>
                <Form.Item>
                  <Button type="primary" htmlType="submit" className="login-form-button">
                    
                  </Button>
                </Form.Item>
              </Form>
              <Tree className="div_box"
                checkable
                defaultExpandAll={true}
                defaultExpandedKeys={[]}
                defaultSelectedKeys={[]}
                defaultCheckedKeys={[]}
                // onSelect={this.onSelect}
                onCheck={this.onCheck}
              >
                <TreeNode style={{ width: '100%' }} title="" key="0">
                  <TreeNode className="div_box_child" title={
                    <div>
                      <div className="div_box_child_top">
                        <div>
                          <img src={require("../../../../imgs/taobao.png")} alt=""/>
                          <span></span>
                        </div>
                        <div className="div_box_child_top_right">
                          <span> [<Link to="/"></Link>]</span>
                          <span>taskAdmin2019-03-14 12:00</span>
                          <span>sdsds<span style={{ color: '#e96262' }}></span></span>
                        </div>
                      </div>
                      {/*  */}
                      <div className="publishTasks_css">
                        <div>
                          <div className="publishTasks_css_child">
                            <img className="goodsImgs" src={require("../../../../imgs/login-bg.png")} alt="sp"/>
                            <p></p>
                          </div>
                          <div>Stateunderway<span>0</span> Toe goods<span>0</span> Tofund<span>0</span> Der sheet<span>0</span> ok<span>0</span></div>
                        </div>
                        {/*   */}
                        <div>
                          <Button type="primary">Ftasks</Button>
                          <p>
                            <img style={{ width: '70px', height: '70px' }} src={require("../../../../imgs/login-bg.png")} alt="pingtai"/>
                          </p>
                        </div>
                      </div>
                    </div>
                  } key="1">
                  </TreeNode>
                  <TreeNode className="div_box_child" title={
                    <div>
                      <div className="div_box_child_top">
                        <div>
                          <img src={require("../../../../imgs/taobao.png")} alt=""/>
                          <span></span>
                        </div>
                        <div className="div_box_child_top_right">
                          <span> [<Link to="/"></Link>]</span>
                          <span>taskAdmin2019-03-14 12:00</span>
                          <span>sdsds<span style={{ color: '#e96262' }}></span></span>
                        </div>
                      </div>
                      {/*  */}
                      <div className="publishTasks_css">
                        <div>
                          <div className="publishTasks_css_child">
                            <img className="goodsImgs" src={require("../../../../imgs/login-bg.png")} alt="sp"/>
                            <p></p>
                          </div>
                          <div>Stateunderway<span>0</span> Toe goods<span>0</span> Tofund<span>0</span> Der sheet<span>0</span> ok<span>0</span></div>
                        </div>
                        {/*   */}
                        <div>
                          <Button type="primary">Ftasks</Button>
                          <p>
                            <img style={{ width: '70px', height: '70px' }} src={require("../../../../imgs/login-bg.png")} alt="pingtai"/>
                          </p>
                        </div>
                      </div>
                    </div>
                  } key="2">
                  </TreeNode>
                </TreeNode>
              </Tree>
              {/*  */}
              <Pagination className="Fpage" onChange={this.paginaTions} onShowSizeChange={this.aaa} defaultCurrent={1} total={15} />
            </div>
          </div>
        </div>
      </div>
    </div>)
  }
}

const PublishTasks = Form.create()(PublishTaskss);
export default PublishTasks
